import React from 'react'
import {connect} from 'react-redux'


import {SongList} from './SongList'




class Welcome extends React.Component{
    handleAudio(){
        document.addEventListener('play', function(e){
            var audios = document.getElementsByTagName('audio');
            for(var i = 0, len = audios.length; i < len;i++){
                if(audios[i] !== e.target){
                    audios[i].pause();
                }
            }
        }, true);
    }


    render() {
        return (
        <div className="container px-0">
            <div className="row text-center px-0 mx-0">
                <div className="col-12 px-0">
                    <h1 className="display-4">PX Music</h1>
                    <p className="p-0 m-0">Tu sitio de descargas y noticias del genero</p> 
                    <div className="col-12 px-0">
                    <div className="alert alert-danger py-2 px-0">

                    <p className="mt-2 p-0 text-center">Lo Mas Nuevo!
                        <br/>  
                    </p>
                    </div>
                </div>              
                </div>

            </div>

            <div id="TEMP-DIV-FOR-DEV">
                <h5 className="card-title  text-center">
                <figure>
                <figcaption>SONG NAME</figcaption>
                </figure>
                <audio controls preload="none" onPlay={this.handleAudio}>
                    <source src='http://res.cloudinary.com/wlopez/video/upload/v1557018224/pxmusic/music/2019/4/Baby_Johnny_Ft._Benny_Benni_Y_Pacho_-_Tus_Amigas_Dicen_Prod._Real_Notaz_Beatz_y_El_Jetty_WWW.ELGENERO.COM_mbqsc4.mp3' type="audio/mpeg"/>
                Your browser does not support the audio element.
                </audio>
                </h5>
                <h5 className="card-title  text-center">
                <figure>
                <figcaption>SONG 2 NAME</figcaption>
                </figure>
                <audio controls preload="none" onPlay={this.handleAudio}>
                    <source src='http://res.cloudinary.com/wlopez/video/upload/v1557018224/pxmusic/music/2019/4/Baby_Johnny_Ft._Benny_Benni_Y_Pacho_-_Tus_Amigas_Dicen_Prod._Real_Notaz_Beatz_y_El_Jetty_WWW.ELGENERO.COM_mbqsc4.mp3' type="audio/mpeg"/>
                Your browser does not support the audio element.
                </audio>
                </h5>

            
            </div>

            <SongList url={'/api/music/all'} perPage={4}/>

        </div>
        )
    }
}





function mapStateToProps(state){
    return {
        
    }
}
export default connect(mapStateToProps)(Welcome)